import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-tac-device-group-form',
  templateUrl: './tac-device-group-form.component.html',
  styleUrls: ['./tac-device-group-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TacDeviceGroupFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading

  objectKeys = Object.keys;


  list = {
    acl: {
      multiple: false,
      title: 'Access Control List',
      title_sidebar: 'ACL List',
      search: true,
      apiurl: 'api/tacacs/acl/list/',
    },
    user_group: {
      multiple: false,
      title: 'Default User Group',
      title_sidebar: 'User Group List',
      search: true,
      apiurl: 'api/tacacs/user/group/list/',
    }
  }

  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

  setAcl(data) {
    // console.log(data)
    this.options.data.acl = data
  }

  setUserGroup(data) {
    //console.log(data)
    this.options.data.user_group = data
  }

}
