export class DeviceGroup {
  acl: any[];
  default_flag: number;
  banner_failed: string;
  banner_motd: string;
  banner_welcome: string;
  connection_timeout: number;
  created_at: string;
  enable: string;
  enable_flag: number;
  id: number;
  key: string;
  manual: string;
  name: string;
  updated_at: string;
  user_group: any[];

    empty(): void {
      this.acl=[];
      this.default_flag=0;
      this.banner_failed='';
      this.banner_motd='';
      this.banner_welcome='';
      this.connection_timeout=undefined;
      this.created_at='';
      this.enable='';
      this.enable_flag=1;
      this.id=undefined;
      this.key='';
      this.manual='';
      this.name='';
      this.updated_at='';
      this.user_group=[];
    }
}
