import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Device
import { DeviceGroup } from '../../../../partials/layout/tacgui/_forms/tac-device-group-form/device-group.model'
//Service//
import { DeviceGroupService } from '../device-group.service'

@Component({
  selector: 'kt-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  formParameters = {
    action: 'add',
    data: new DeviceGroup,
    old_data: new DeviceGroup
  }

  constructor(
    private toastr:ToastrService,
    private devG_service: DeviceGroupService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  add(){
    // console.log(this.formParameters.data)
    let data = this.makeClone(this.formParameters.data)
    data.acl = (data.acl[0] && data.acl[0].id) ? data.acl[0].id : null
    data.user_group = (data.user_group[0] && data.user_group[0].id) ? data.user_group[0].id : null
    // console.log(data)
    this.loadingForm.next(true)
    this.devG_service.add(data).subscribe(data => {
      // console.log(data)
      this.validation.next(data.error.validation)
      if(data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.deviceGroup) {
        this.toastr.success('Device Group Added!');
        this.router.navigate(['../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }


}
